body {
  margin: 0 0;
  padding: 0 0;
  font-family: 'Inter', sans-serif;
  color: $text-color;
  font-weight: 500;
}
ul {
  list-style: none;
}
img {
  display: block;
}
.section {
  padding: 40px 0;
}
.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $accent-color;
  border-radius: 5px;
  width: 200px;
  height: 42px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.1875;
  text-align: center;
  color: $white-text-color;
  cursor: pointer;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.icon {
  width: 40px;
  height: 40px;
  color: $white-text-color;
  background-color: $accent-color;
  border-radius: 50%;
}
@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}
